<template>
  <div>
    <a-page-header
      title="ÊâπÈáèËØÜÂà´"
      sub-title="‰∏ä‰º†Â§öÂº†Èù¢ÊñôÂõæÁâáËøõË°åÊâπÈáèÊô∫ËÉΩÂàÜÁ±ªËØÜÂà´"
      @back="$router.go(-1)"
    />

    <a-row :gutter="32">
      <a-col :xs="24" :lg="8">
        <a-card title="ÈÄâÊã©Ê®°Âûã" style="margin-bottom: 24px;">
          <a-select
            v-model:value="selectedModel"
            placeholder="ËØ∑ÈÄâÊã©ËØÜÂà´Ê®°Âûã"
            style="width: 100%"
            @change="onModelChange"
          >
            <a-select-option v-for="model in models" :key="model.id" :value="model.id">
              <a-space>
                <a-tag :color="model.type === 'binary' ? 'blue' : 'green'" size="small">
                  {{ model.type === 'binary' ? '‰∫åÂàÜÁ±ª' : 'Â§öÂàÜÁ±ª' }}
                </a-tag>
                {{ model.name }}
              </a-space>
            </a-select-option>
          </a-select>
          <p v-if="currentModelInfo" style="margin-top: 8px; color: #666; font-size: 14px;">
            {{ currentModelInfo.description }}
          </p>
        </a-card>

        <a-card title="‰∏ä‰º†ÂõæÁâá">
          <a-upload-dragger
            v-model:fileList="fileList"
            :before-upload="beforeUpload"
            multiple
            accept="image/*"
            @change="handleUpload"
          >
            <div style="padding: 40px;">
              <p class="ant-upload-drag-icon">
                <inbox-outlined style="font-size: 48px; color: #1890ff;" />
              </p>
              <p class="ant-upload-text">ÁÇπÂáªÊàñÊãñÊãΩÂõæÁâáÂà∞Ê≠§Âå∫Âüü‰∏ä‰º†</p>
              <p class="ant-upload-hint">ÊîØÊåÅÂ§öÈÄâÔºåJPG„ÄÅPNG„ÄÅBMP„ÄÅWEBP Ê†ºÂºè</p>
            </div>
          </a-upload-dragger>

          <div v-if="imageDataList.length > 0" style="margin-top: 16px;">
            <a-space>
              <a-tag color="blue">Â∑≤ÈÄâÊã© {{ imageDataList.length }} Âº†ÂõæÁâá</a-tag>
              <a-button size="small" @click="clearImages">Ê∏ÖÁ©∫</a-button>
            </a-space>
          </div>

          <div style="margin-top: 16px; text-align: center;">
            <a-button
              type="primary"
              size="large"
              :loading="predicting"
              :disabled="imageDataList.length === 0 || !selectedModel"
              @click="batchPredict"
            >
              <template #icon>
                <thunderbolt-outlined />
              </template>
              ÊâπÈáèËØÜÂà´ ({{ imageDataList.length }})
            </a-button>
          </div>
        </a-card>
      </a-col>

      <a-col :xs="24" :lg="16">
        <a-card title="ËØÜÂà´ÁªìÊûú">
          <template #extra v-if="results.length > 0">
            <a-space>
              <a-button size="small" @click="exportResults">
                <template #icon>
                  <download-outlined />
                </template>
                ÂØºÂá∫ÁªìÊûú
              </a-button>
              <a-button size="small" @click="clearResults">Ê∏ÖÁ©∫ÁªìÊûú</a-button>
            </a-space>
          </template>

          <div v-if="results.length === 0 && !predicting" style="text-align: center; padding: 60px 20px; color: #999;">
            <folder-open-outlined style="font-size: 64px; margin-bottom: 16px;" />
            <p>ËØ∑‰∏ä‰º†ÂõæÁâáÂπ∂ÈÄâÊã©Ê®°ÂûãËøõË°åÊâπÈáèËØÜÂà´</p>
          </div>

          <a-spin :spinning="predicting" style="width: 100%;">
            <div v-if="predicting" style="text-align: center; padding: 40px;">
              <p>Ê≠£Âú®Â§ÑÁêÜ {{ imageDataList.length }} Âº†ÂõæÁâá...</p>
              <a-progress :percent="Math.round((processedCount / imageDataList.length) * 100)" />
            </div>

            <div v-if="results.length > 0">
              <a-row :gutter="[16, 16]">
                <a-col :xs="24" :sm="12" :lg="8" v-for="(result, index) in results" :key="index">
                  <a-card size="small" hoverable>
                    <template #cover>
                      <div style="height: 200px; overflow: hidden; display: flex; align-items: center; justify-content: center; background: #f5f5f5;">
                        <img
                          :src="imageDataList[result.index]"
                          style="max-width: 100%; max-height: 100%; object-fit: contain;"
                        />
                      </div>
                    </template>
                    
                    <div v-if="result.success">
                      <a-space direction="vertical" style="width: 100%;">
                        <a-tag :color="result.prediction.confidence > 0.8 ? 'green' : result.prediction.confidence > 0.6 ? 'orange' : 'blue'">
                          {{ result.prediction.class }}
                        </a-tag>
                        <a-progress
                          :percent="result.prediction.confidence * 100"
                          size="small"
                          :stroke-color="result.prediction.confidence > 0.8 ? '#52c41a' : result.prediction.confidence > 0.6 ? '#faad14' : '#1890ff'"
                        />
                        <small style="color: #666;">
                          ÁΩÆ‰ø°Â∫¶: {{ (result.prediction.confidence * 100).toFixed(1) }}%
                        </small>
                      </a-space>
                    </div>
                    
                    <div v-else>
                      <a-alert type="error" :message="result.error" size="small" />
                    </div>
                  </a-card>
                </a-col>
              </a-row>

              <a-divider />
              
              <a-row :gutter="32">
                <a-col :xs="24" :md="6">
                  <a-statistic title="ÊÄªËÆ°" :value="results.length" suffix="Âº†" />
                </a-col>
                <a-col :xs="24" :md="6">
                  <a-statistic title="ÊàêÂäü" :value="successCount" suffix="Âº†" />
                </a-col>
                <a-col :xs="24" :md="6">
                  <a-statistic title="Â§±Ë¥•" :value="failCount" suffix="Âº†" />
                </a-col>
                <a-col :xs="24" :md="6">
                  <a-statistic title="ÊàêÂäüÁéá" :value="successRate" suffix="%" />
                </a-col>
              </a-row>
            </div>
          </a-spin>
        </a-card>
      </a-col>
    </a-row>
  </div>
</template>

<script>
import { InboxOutlined, ThunderboltOutlined, FolderOpenOutlined, DownloadOutlined } from '@ant-design/icons-vue'
import axios from 'axios'

export default {
  name: 'BatchPredict',
  components: {
    InboxOutlined,
    ThunderboltOutlined,
    FolderOpenOutlined,
    DownloadOutlined
  },
  data() {
    return {
      models: [],
      selectedModel: null,
      fileList: [],
      imageDataList: [],
      predicting: false,
      results: [],
      processedCount: 0
    }
  },
  computed: {
    currentModelInfo() {
      return this.models.find(m => m.id === this.selectedModel)
    },
    successCount() {
      return this.results.filter(r => r.success).length
    },
    failCount() {
      return this.results.filter(r => !r.success).length
    },
    successRate() {
      return this.results.length > 0 ? Math.round((this.successCount / this.results.length) * 100) : 0
    }
  },
  mounted() {
    this.loadModels()
    if (this.$route.query.model) {
      this.selectedModel = this.$route.query.model
    }
  },
  methods: {
    async loadModels() {
      try {
        const response = await axios.get('/api/models')
        this.models = response.data.models
        
        if (!this.selectedModel && this.models.length > 0) {
          this.selectedModel = this.models[0].id
        }
      } catch (error) {
        this.$message.error('Âä†ËΩΩÊ®°ÂûãÂ§±Ë¥•: ' + error.message)
      }
    },
    
    onModelChange() {
      this.results = []
    },
    
    beforeUpload(file) {
      const isImage = file.type.startsWith('image/')
      if (!isImage) {
        this.$message.error('Âè™ËÉΩ‰∏ä‰º†ÂõæÁâáÊñá‰ª∂!')
        return false
      }
      
      const isLt10M = file.size / 1024 / 1024 < 10
      if (!isLt10M) {
        this.$message.error('ÂõæÁâáÂ§ßÂ∞è‰∏çËÉΩË∂ÖËøá 10MB!')
        return false
      }
      
      return false
    },
    
    handleUpload(info) {
      const files = info.fileList.map(f => f.originFileObj || f)
      this.processFiles(files)
    },
    
    processFiles(files) {
      this.imageDataList = []
      const promises = files.map(file => {
        return new Promise((resolve) => {
          const reader = new FileReader()
          reader.onload = (e) => resolve(e.target.result)
          reader.readAsDataURL(file)
        })
      })
      
      Promise.all(promises).then(results => {
        this.imageDataList = results
        this.results = []
      })
    },
    
    clearImages() {
      this.fileList = []
      this.imageDataList = []
      this.results = []
    },
    
    clearResults() {
      this.results = []
    },
    
    async batchPredict() {
      if (this.imageDataList.length === 0 || !this.selectedModel) {
        this.$message.warning('ËØ∑‰∏ä‰º†ÂõæÁâáÂπ∂ÈÄâÊã©Ê®°Âûã')
        return
      }
      
      this.predicting = true
      this.processedCount = 0
      
      try {
        const response = await axios.post('/api/batch_predict', {
          model_id: this.selectedModel,
          images: this.imageDataList
        })
        
        this.results = response.data.results
        this.processedCount = this.imageDataList.length
        
        this.$message.success(`ÊâπÈáèËØÜÂà´ÂÆåÊàê! ÊàêÂäü: ${this.successCount}, Â§±Ë¥•: ${this.failCount}`)
      } catch (error) {
        this.$message.error('ÊâπÈáèËØÜÂà´Â§±Ë¥•: ' + (error.response?.data?.error || error.message))
      } finally {
        this.predicting = false
      }
    },
    
    exportResults() {
      if (this.results.length === 0) return
      
      const csvContent = [
        ['Â∫èÂè∑', 'È¢ÑÊµãÁ±ªÂà´', 'ÁΩÆ‰ø°Â∫¶', 'Áä∂ÊÄÅ'],
        ...this.results.map((result, index) => [
          index + 1,
          result.success ? result.prediction.class : 'ËØÜÂà´Â§±Ë¥•',
          result.success ? (result.prediction.confidence * 100).toFixed(2) + '%' : '-',
          result.success ? 'ÊàêÂäü' : result.error
        ])
      ].map(row => row.join(',')).join('\n')
      
      const blob = new Blob(['\ufeff' + csvContent], { type: 'text/csv;charset=utf-8;' })
      const link = document.createElement('a')
      link.href = URL.createObjectURL(blob)
      link.download = `ÊâπÈáèËØÜÂà´ÁªìÊûú_${new Date().toISOString().slice(0, 19).replace(/:/g, '-')}.csv`
      link.click()
    }
  }
}
</script>

<style scoped>
.ant-upload-dragger {
  background: #fafafa !important;
}

.ant-upload-dragger:hover {
  border-color: #1890ff !important;
}
</style>
